import React, { useState } from "react";
import { db } from "./firebase";
import { collection, addDoc, serverTimestamp } from "firebase/firestore";

export default function UploadBlog() {
  const [data, setData] = useState({
    id: "",
    title: "",
    author: "",
    full: "",
    date: "",
  });

  const [image, setImage] = useState(null);
  const [imageURL, setImageURL] = useState("");

  const uploadImage = async () => {
    if (!image) {
      alert("Please select an image");
      return;
    }

    const formData = new FormData();
    formData.append("file", image);
    formData.append("upload_preset", "the gudie");
    formData.append("cloud_name", "dwzultetk");

    const res = await fetch(
      "https://api.cloudinary.com/v1_1/dwzultetk/image/upload",
      {
        method: "POST",
        body: formData,
      }
    );

    const uploaded = await res.json();
    setImageURL(uploaded.secure_url);
    return uploaded.secure_url;
  };

  const handleSubmit = async () => {
    const url = await uploadImage();
    if (!url) return;

    const blogData = {
      ...data,
      image: url,
      createdAt: serverTimestamp(),
    };

    await addDoc(collection(db, "blogs"), blogData);

    alert("Blog uploaded successfully!");
    console.log("Saved Blog:", blogData);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100 px-4">
      <div className="w-full max-w-xl bg-white p-8 rounded-2xl shadow-lg">
        <h2 className="text-2xl font-semibold mb-6 text-center text-gray-800">
          Upload Blog
        </h2>

        {/* Form Inputs */}
        <div className="space-y-4">

          <input
            type="text"
            placeholder="Blog ID"
            className="w-full p-3 border rounded-lg focus:ring focus:ring-blue-300"
            onChange={(e) => setData({ ...data, id: e.target.value })}
          />

          <input
            type="text"
            placeholder="Title"
            className="w-full p-3 border rounded-lg focus:ring focus:ring-blue-300"
            onChange={(e) => setData({ ...data, title: e.target.value })}
          />

          <input
            type="text"
            placeholder="Author"
            className="w-full p-3 border rounded-lg focus:ring focus:ring-blue-300"
            onChange={(e) => setData({ ...data, author: e.target.value })}
          />

          <input
            type="text"
            placeholder="Date (optional)"
            className="w-full p-3 border rounded-lg focus:ring focus:ring-blue-300"
            onChange={(e) => setData({ ...data, date: e.target.value })}
          />

          <textarea
            rows="5"
            placeholder="Full Content"
            className="w-full p-3 border rounded-lg focus:ring focus:ring-blue-300"
            onChange={(e) => setData({ ...data, full: e.target.value })}
          ></textarea>

          {/* Image upload */}
          <div>
            <label className="block font-medium mb-1">Upload Image</label>
            <input
              type="file"
              className="w-full p-2 border rounded-lg"
              onChange={(e) => setImage(e.target.files[0])}
            />
          </div>
        </div>

        {/* Submit Button */}
        <button
          onClick={handleSubmit}
          className="w-full mt-6 bg-blue-600 hover:bg-blue-700 text-white py-3 rounded-xl font-medium transition"
        >
          Upload Blog + Image
        </button>

        {/* Image Preview */}
        {imageURL && (
          <div className="mt-6">
            <h3 className="font-medium text-gray-700 mb-2">Uploaded Image</h3>
            <img
              src={imageURL}
              alt="Uploaded"
              className="rounded-xl shadow-md"
            />
          </div>
        )}
      </div>
    </div>
  );
}
