import { useState, useEffect } from "react";
import { ArrowRight } from "lucide-react";
import Papa from "papaparse";
import { Link } from "react-router-dom";

export default function Slider() {
  const [techData, setTechData] = useState([]);
  const [current, setCurrent] = useState(0);

  const sheetUrl =
    "https://docs.google.com/spreadsheets/d/e/2PACX-1vQeomN__blTC2EylL4uEwGxLXMh0zxQ0F5BlWwBNHySrtJwqw9aJe9Zw0166-pDhaboaM4GdTlhzAET/pub?gid=0&single=true&output=csv";

  useEffect(() => {
    Papa.parse(sheetUrl, {
      download: true,
      header: true,
      complete: (result) => {
        let data = result.data;
        data = data.filter((item) => item.id);
        data = data.map((d) => ({ ...d, id: Number(d.id) }));
        data.sort((a, b) => b.id - a.id);
        data = data.slice(0, 8);
        setTechData(data);
      },
    });
  }, []);

  const nextSlide = () => {
    setCurrent((prev) => (prev === techData.length - 1 ? 0 : prev + 1));
  };

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrent((prev) => (prev === techData.length - 1 ? 0 : prev + 1));
    }, 7000);
    return () => clearInterval(interval);
  }, [techData.length]);

  return (
    <div className="relative h-screen w-full overflow-hidden bg-white">
      {techData.map((slide, index) => (
        <div
          key={slide.id}
          className={`absolute inset-0 transition-opacity duration-1000 ease-in-out ${
            index === current ? "opacity-100" : "opacity-0 pointer-events-none"
          }`}
        >
          <div className="flex flex-col md:flex-row items-center justify-center h-full px-6 md:px-16 gap-10">
            
            {/* LEFT: Image */}
            <div className="w-full md:w-1/2 flex justify-center">
              <img
                src={slide.image}
                alt={slide.title}
                className="w-[90%] h-[400px] object-cover rounded-2xl shadow-lg"
              />
            </div>

            {/* RIGHT: Text */}
            <div className="w-full md:w-1/2 text-center md:text-left flex flex-col justify-center items-center md:items-start">
              <h1 className="text-2xl md:text-3xl font-semibold text-[#2b0d0d] mb-4 w-[90%]">
                {slide.title}
              </h1>

              <p className="text-lg text-gray-700 mb-4 w-[90%] leading-relaxed">
                {slide.description
           }
                {slide.description?.split(" ").length > 50 && (
                  <>
                    ...{" "}
                    <Link
                      to={`/individual/${slide.id}`}
                      state={{ article: slide }}
                      className="text-[#2b0d0d] font-semibold underline hover:text-[#451515] transition"
                    >
                      Read More
                    </Link>
                  </>
                )}
              </p>

              <p className="text-lg text-gray-600 mb-6">{slide.author}</p>

              {/* BUTTONS ROW: READ + RIGHT ARROW */}
              <div className=" absolute mt-[260px]  flex items-center gap-4">
                {/* READ BUTTON */}
                <Link
                  to={`/individual/${slide.id}`}
                  state={{ article: slide }}
                  className="bg-[#2b0d0d] text-white px-5 py-2 rounded-full hover:bg-[#451515] transition"
                >
                  Read
                </Link>

                {/* RIGHT ARROW BUTTON */}
                <button
                  onClick={nextSlide}
                  className="bg-[#2b0d0d] p-3 rounded-full hover:bg-[#451515] transition transform hover:translate-x-1"
                >
                  <ArrowRight className="text-white w-5 h-5" />
                </button>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
